.container {
    width: 280px;
    // height: 225px;
    padding: 12px 17px;
    background: #333846;
    font-size: 16px;
    position: absolute;
    transition: right 0.3s;
    color: white;
    top: 64px;
    border-radius: 4px;

    > div {
        padding: 8px 0;

        &:not(:nth-of-type(1)) {
            border-top: 1px solid rgba(255, 255, 255, 0.3);
        }
    }
}

.hidden {
    right: 6px;
}

.not-hidden {
    right: 356px;
}
