.switch {
    background-color: #7a7a7a !important;
    min-width: 34px !important;
    height: 14px !important;

    &::after {
        top: -4px !important;
        left: 0 !important;
        width: 20px !important;
        height: 20px !important;
        background-color: #c4c4c4 !important;
    }
}

.symbol-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
    margin-right: 70px;

    > div {
        display: flex;
        align-items: center;
    }

    :global(.ant-switch-checked) {
        background-color: #01a674 !important;

        &::after {
            left: 34px !important;
            background-color: #0ce0b7 !important;
        }
    }
}