.container {
    height: 100%;
    background: #1b2330;
    position: relative;
    display: flex;
    overflow: hidden;
}

.header {
    position: absolute;
    left: 24px;
    top: 24px;
    display: flex;
    align-items: center;

    img {
        width: 32px;
        height: 32px;
    }

    h1 {
        font-size: 20px;
        color: rgba(255, 255, 255, 0.75);
        margin: 0 0 0 16px;
    }
}
